.notfound-container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #f8fafc 0%, #e3e9f7 100%);
  text-align: center;
  padding: 2rem;
}

.notfound-animation {
  margin-bottom: 2rem;
}

.notfound-title {
  font-size: 6rem;
  color: #ff6600;
  font-weight: 900;
  margin-bottom: 0.5rem;
  letter-spacing: 2px;
}

.notfound-message {
  font-size: 1.4rem;
  color: #333;
  margin-bottom: 2rem;
}

.notfound-home-btn {
  display: inline-block;
  background: #ff6600;
  color: #fff;
  font-size: 1.1rem;
  font-weight: bold;
  padding: 0.8rem 2rem;
  border-radius: 30px;
  text-decoration: none;
  transition: all 0.2s ease;
  box-shadow: 0 2px 10px rgba(255, 102, 0, 0.15);
}

.notfound-home-btn:hover {
  background: #e65c00;
  transform: scale(1.05);
}
